
.prestations h2{
  color: #b5723c;
}

.presta-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.presta-item{
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.presta-background{
  width: 100%;
  height: 80%;
  background-size: cover;
  background-position: center;
}

.presta-text{
  height: 20%;
  background: #b5723c;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

#hidden-prestations{
  max-height: 0;
  -webkit-transition: -webkit-transform 2s linear;
  transition: -webkit-transform 2s linear;
  -o-transition: transform 2s linear;
  transition: transform 2s linear;
  transition: transform 2s linear, -webkit-transform 2s linear;
  overflow: hidden;
}

#hidden-prestations.slide-down{
  max-height: inherit;
}

#hidden-prestations.slide-up{
  max-height: 0px;
}

#button-container{
  margin-top: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

#button-container button{
  border: 2px solid #ffffff;
  color: #ffffff;
  border-radius: 4px;
  background-color: transparent;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
}

#button-container button:hover{
  cursor: pointer;
}

#button-container .more{
  position: absolute;
  right: 50;
  font-size: 40px;
  color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.more:hover{
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

@media screen and (max-width: 768px){
  .presta-item{
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
  }
}



